.App {
  font-family: "Courier New", Courier, monospace sans-serif;
  text-align: center;
}

.navbar {
  display: flex;
  padding: 10px 20px;
  background-color: rgb(37, 33, 39);
  justify-content: space-between;
}
.radioName{
  font-size: 30px;
}
input[type="radio"] {
  width: 20px;
  height: 1.5em;
}

body {
  width: auto;
  height: 100vh; 
  background-image: url("./Photo/show.png");
  margin: 0;
  padding: 0;
}
.button{
  background-color:rgb(122, 177, 190);
  color: black;
  padding: 5px;
  border-radius: 5px;
  font-size: large;
}
.main-container {
  margin-top: 50px;
}

.container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 50px;
}

.sub-container {
  flex-direction: column;
  margin: 20px;
  background-color: black;
  border-radius: 10px;
  padding: 10px;
  color: rgb(17, 230, 17);
}

.inner-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 300px;
  color: rgb(17, 230, 17);
}

.input {
  width: 400px;
  height: 25px;
  font-size: larger;
}

.search {
  color: rgb(17, 230, 17);
  margin-top: 2em;
}

.rating {
  color: white;
  display: flex;
  margin-top: 5px;
  margin-left: 2px;
}

.img {
  width: 290px;
  height: 325px;
  border-radius: 10px;
}

.sub-container:hover {
  background: linear-gradient(0deg, rgb(15, 9, 9), rgb(133, 224, 236));
  transform: scale(1.1);
}

@media screen and (max-width:600px) {
    body {
      width: auto;
      height: 100vh;
      background-image: url("https://c4.wallpaperflare.com/wallpaper/460/140/13/sci-fi-futuristic-city-dark-theme-fantasy-wallpaper-preview.jpg");
    }

    .input {
      width: 300px;
      height: 20px;
      font-size: medium;
    }

    .navImg{
      width: 150px;
      height: 50px;
    }
    input[type="radio"] {
        width: 15px;
        height: 0.8em;
    }
    .radioName {
      font-size: 18px;
    }
    .main-container {
      margin-top: 30px;
    }
    .container {
      margin-top: 30px;
    }
    .sub-container {    
        margin: 10px;       
        width: 200px;      
    }
    .inner-container {      
      width: 200px;
    }

    .img {
      width: 190px;
      height: 250px;
    }
}